var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("达斯·维德")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        达斯维德是星球大战系列中的虚构角色。他是原版三部曲中的主要对手，但作为Anakin Skywalker，是前传三部曲的主要主角。“星球大战”的创作者乔治·卢卡斯（George Lucas）将这一特许经营的前六部电影称为“ 达斯·维德的悲剧 ”。")]),_c('blockquote',{staticClass:"blockquote"},[_vm._v("        Anapin Skywalker 原本是一名绝地预言会给部队带来平衡，被帕尔帕廷引诱到了原力的黑暗面，帕尔帕廷是秘密的西斯勋爵。在与他的前任导师欧比万克诺比（Obi-Wan Kenobi）进行光剑战斗后，他被肢解，Vader变成了一个半机械人。然后，他为达斯维德服务于银河帝国，直到他从帕尔帕廷拯救他的儿子卢克天行者，在这个过程中牺牲自己的生命来赎回自己。他也是帕德米阿米达拉的秘密丈夫莱娅公主的父亲和Kylo Ren的祖父，星球大战续集三部曲的主要反派。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260","src":""}})],1),_c('br'),_c('h2',[_vm._v("创作背景")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('br'),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        系列创作者乔治卢卡斯最初将该系列的主要反派与卢克史塔克勒的父亲安尼金分开。角色的各种名称组合建立在短语“黑暗水”上。然后Lucas“添加了许多姓氏，Vaders和Wilsons以及Smiths，他刚刚想出了Darth和Vader的组合。” 发行后帝国反击战（1980年），卢卡斯表示，取名维达是基于德国/荷兰语同音 壶腹部或者维达，意为“父亲”，让代表“暗黑之父”之称。然而，没有证据表明卢卡斯在1978年之前将维德视为卢克的父亲。其他可能激发了这个名字的词是“死亡”和“入侵者”。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        在“ 星球大战”的初稿中，身材高大，严峻的将军“达斯维达”已经与他的最终描写一致，而主人公安妮·斯塔克勒的角色与他儿子卢克的16岁儿子相似。一位受人尊敬的战士。最初，卢卡斯认为西斯是一个服务皇帝的团体，就像施魏斯特菲尔为阿道夫希特勒服役一样。在为“帝国反击战 ”制作背景故事时，卢卡斯将其浓缩为达斯维德形式的一个角色。")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180","height":"240","src":""}}),_c('v-card-title',{attrs:{"primary-title":""}},[_c('div',[_c('span')])])],1)],1),_c('br')])}
